import React from 'react';

const EmojiPicker = ({ setNewMessage, newMessage, handleSend }) => {
  const emojis = ['😊', '😂', '😢', '😍', '😎', '🎉', '🎁', '😜', '🤔', '😇', '🙃', '😅', '🤣', '🤩', '😡', '🥳', '🤯', '😱', '🤗', '😏', '😴', '🤤', '😷', '🤒', '🤕', '🤮', '🤑', '😈', '👻', '💩', '😺', '👀', '💪', '🙏', '👏', '🙌', '👐', '✌️', '👍', '👎', '👊', '🤟'];

  return (
    <div style={styles.emojiPicker}>
      {emojis.map((emoji) => (
        <span
          key={emoji}
          role="button"
          tabIndex={0}
          onClick={() => { 
            setNewMessage(`${newMessage}${emoji}`); 
            handleSend(); 
          }}
          onKeyDown={(e) => {
            if (e.key === 'Enter') {
              setNewMessage(`${newMessage}${emoji}`);
              handleSend();
            }
          }}
          style={styles.emojiButton}
        >
          {emoji}
        </span>
      ))}
    </div>
  );
};

const styles = {
  emojiPicker: {
    marginTop: '10px',
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    padding: '10px',
    border: '2px solid #5a9f98',
    borderRadius: '8px',
    backgroundColor: '#000',
    color: '#fff',
  },
  emojiButton: {
    fontSize: '24px',
    margin: '5px',
    cursor: 'pointer',
  },
};

export default EmojiPicker;
